import {Component, OnInit} from '@angular/core';
import {Quote} from "./Quote";
import {Observable} from "rxjs/Observable";
import {environment} from "../environments/environment";


@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent implements OnInit{
  title = 'app works!';
  qts: Quote[];
  quotes: Observable<Quote[]>;
  EventSource: any = window['EventSource'];

  constructor() {
    this.qts=[];
  }

  ngOnInit(): void {
    //this.quotes= this.observeStream("http://localhost:8081/subscribe/ers");
    const eventSource = new this.EventSource( environment.url + "/subscribe/ers");
    eventSource.onmessage = event => {
      console.log(event.data);
      let data = JSON.parse(event.data);
    };
  }

  observeStream(sseUrl: string): Observable<Quote> {
    return new Observable<Quote>(obs => {
      const eventSource = new this.EventSource(sseUrl);
      eventSource.onmessage = event => {
        console.log(event.data);
        let data = JSON.parse(event.data);
      };
      return () => eventSource.close();
    });
  }
}



